<template>
  <div class="login text-center ">
    <div class="bg-primary-gradient">
      <div class="logo">
        <v-img
            :src="require('@/assets/logo-white.png')"
            contain
            height="176"
            class="mb-5"
        />
      </div>

      <div class="call-to-action" style="margin: 1rem">
        <div class="text">
          <div>Costo del servicio : S/. {{ consultation.price }}</div>
          <div>Código de solicitud: {{ consultation.id | padleft }}</div>
          <div>Abogado responsable: LegalHelp</div>
        </div>
      </div>

      <div class="call-to-action-white" style="margin: 1rem; position: relative">
        <div class="text">
          ¡Tú recibo ha sido cargado con éxito!<br>
          Validaremos el recibo en breve.
        </div>
        <img :src="require('@/assets/icons/lawyer-07.svg')" style="position: absolute; right: 0; bottom: 0">
      </div>

      <div style="width: 60%; margin: 0 auto">
        <v-img
            :src="require('@/assets/confirm.svg')"
        />
      </div>

      <div class="mt-5">
        <v-btn @click="goToRequests">Ir a Estado de tus consultas</v-btn>
      </div>
    </div>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
  </div>
</template>
<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'RequestPendingConfirm',
  data: () => ({
    isLoading: false,
    consultation: {
      status: ''
    },
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  created() {
    this.loadConsultation()
  },
  methods: {
    loadConsultation() {
      let consultationId = this.$route.params.id
      let url = 'consultations/' + consultationId
      axios.get(url).then((response) => {
        this.consultation = response.data
      })
    },
    goToRequests() {
      this.$router.push({name: 'RequestIndex'})
    }
  }
}
</script>